<template>
  <div class="info-page">
    <!-- <div class="content" v-html="convertedMarkdown"></div> -->
    <div class="content">
      <img :src="person.headImg">
      <h4>{{ person.name }}</h4>
      <p>{{ person.duties }}</p>
      <p>{{ person.level }}</p>
      <p>{{ person.phoneNumber }}</p>
      <p>xxx</p>
    </div>
  </div>
</template>

<script>
import { marked } from 'marked';

export default {
  data() {
    return {
      members: [
        {
          name: '',
          duties: '',
          level: '',
          phoneNumber: '15348166888',
          headImg: require('@/assets/img/jinzehong.jpg')
        },
        {
          name: '',
          duties: '',
          level: '',
          phoneNumber: '18909098989',
          headImg: require('@/assets/img/tongxiaomeng.jpg')
        },
        {
          name: '',
          duties: '',
          level: '',
          phoneNumber: '13909096966',
          headImg: require('@/assets/img/duchong.jpg')
        }, {
          name: '',
          duties: '',
          level: '',
          phoneNumber: '18990927698',
          headImg: require('@/assets/img/liuwenju.jpg')

        }, {
          name: '',
          duties: '',
          level: '',
          phoneNumber: '13080392515',
          headImg: require('@/assets/img/zhutao.jpg')

        },
        {
          name: '',
          duties: '',
          level: '',
          phoneNumber: '13684175751',
          headImg: require('@/assets/img/yangyongkang.jpg')

        },
        {
          name: '',
          duties: '',
          level: '',
          phoneNumber: '18990927698',
          headImg: require('@/assets/img/ouguiying.jpg')

        }
      ],
      markdown: ``,
    };
  },
  created() {
    console.log(this.$route);
    this.id = this.$route.query.id;
    console.log(this.id);
  },
  computed: {
    convertedMarkdown() {
      return marked(this.markdown);
    },
    person() {
      return this.members.find((e) => { return e.name == this.id })
    }
  },
};
</script>

<style scoped>
.info-page {
  display: flex;
  justify-content: center;
  width: 100%;
}

.content {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content img {
  width: 184px;
  height: 262px;
}
</style>



